import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import {AddUserService} from "../../Services/add-user.service"
import { Router, ActivatedRoute, ParamMap } from "@angular/router";
import {
  AngularFireStorage,
  AngularFireUploadTask,
} from '@angular/fire/storage';
@Component({
  selector: 'app-add-time-line',
  templateUrl: './add-time-line.component.html',
  styleUrls: ['./add-time-line.component.css', '../Admin-common.css','../../Commoncss/popup.css']
})
export class AddTimeLineComponent implements OnInit {
  sub: any;
  docid: string;
  topic;
  Discription;
  Message;
  Date;
  Link;
  TimeLineDetails;
  Messages;
  UpdateMessage;
  isLoading:boolean = false
  OverLay:boolean = false
  public now: Date = new Date();
  uploads;
  UploadedImages =[];
  LoaderMessage;
  Now = new Date();
  constructor(public _location:Location,public AddUserService:AddUserService,
    public ActivatedRoute:ActivatedRoute
    ,private afStorage: AngularFireStorage,
    public Router:Router) { }

  ngOnInit(): void {

    this.sub = this.ActivatedRoute.paramMap.subscribe((params) => {
      this.docid =  params.get("docid")
    })

 
  }




  createTimeLine()
  {
    this.LoaderMessage = "Creating Timeline...."
    this.isLoading = true
     this.UpdateMessage = "Event Created Successfully"
    var dateObj = new Date(this.Date);
    var month = dateObj.getMonth() + 1; //months from 1-12
    var day = dateObj.getUTCDate();
    var year = dateObj.getUTCFullYear() ;
    const newdate = year + "/" + month + "/" + day ;
    const SingleMonth =  dateObj.toLocaleString('default', { month: 'short' })

    this.TimeLineDetails ={
      Topic:this.topic,
      Discription:this.Discription,
      Date:this.Date,
      Link:this.Link,
      Images:this.UploadedImages,
      Archived:"false",
      Close:null,
      openDate:null
    }
    this.Messages ={
      Message:this.Message,
      Time:this.now,
      User:"Owner"
    }

    this.AddUserService.CreateTimeLine(this.docid,SingleMonth,this.TimeLineDetails).then(element => {
      const UserId =  JSON.parse(localStorage.getItem('CreateTimelineId'));
      this.UpdateTicket(SingleMonth,UserId)
      this.AddUserService.CreateTChat(this.docid,UserId,this.Messages).then(element => {
        this.isLoading = false
        this.OverLay = true
      })
      //CreateTimelineId
    })

    //CreateTChat(this.docid,this.Messages)
    //CreateTimeLine(this.docid,month,this.TimeLineDetails)
  }
  importImages(event) {
    this.LoaderMessage = "Uploading Images..."
    this.isLoading = true
    this.uploads = [];

    const filelist = event.target.files;
    if (filelist.length > 8) {
      this.UpdateMessage = "Upload Maximum  8 Images" 
      this.OverLay = true
          this.isLoading = false
    }
    else {
      for (const file of filelist) {
        if (file.size > 2000000) {   
          this.UpdateMessage = "Images Size must be 2MB"
          this.OverLay = true
          this.isLoading = false
        }
        else {

          const path = `sellerfiles/${file.name}`;
          const ref = this.afStorage.ref(path);
          const task = this.afStorage.upload(path, file);
          const _percentage$ = task.percentageChanges();
          // create composed objects with different information. ADAPT THIS ACCORDING to YOUR NEED
          const uploadTrack = {
            fileName: file.name,
          }
          // push each upload into the array
          this.uploads.push(uploadTrack);
          if (this.uploads > 8) {
            this.UpdateMessage = "Upload Max 8 Images"
            this.OverLay = true
            this.isLoading = false
          }
          else {
            // for every upload do whatever you want in firestore with the uploaded file
            const _t = task.then((f) => {
              return f.ref.getDownloadURL().then((url) => {
                this.UploadedImages.push(url)
                // return this.afs.collection('files').add({
                //   name: f.metadata.name,

                // });
                this.isLoading = false
              })
            })
          }

        }
      }
    }
  
  }



  backed() {
 // this._location.back()
 this.OverLay = false

 this.Router.navigate(['/UserDetail/'+ this.docid + '/time'])
  }

 
  UpdateTicket(month,UserDetail)
  {
    this.TimeLineDetails ={
      Id:UserDetail,
      Topic:this.topic,
      Discription:this.Discription,
      Date:this.Date,
      Link:this.Link,
      Images:this.UploadedImages,
      Archived:"false",
      Close:null
    }
    
    this.AddUserService.updateTimeline(this.docid,month,UserDetail,this.TimeLineDetails).then(element => {
  
    })
  }
}
