import { Component, OnInit , AfterViewChecked, ElementRef, ViewChild} from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from "@angular/router";
import {AddUserService} from "../../Services/add-user.service"
import { AngularFirestore } from "@angular/fire/firestore";
import { map, startWith, take, ignoreElements } from "rxjs/operators";
import { ScrollToBottomDirective } from '../../scroll-to-bottom.directive'

@Component({
  selector: 'app-dashboard-tickets',
  templateUrl: './dashboard-tickets.component.html',
  styleUrls: ['./dashboard-tickets.component.css','../Admin-common.css','../../Commoncss/popup.css','../../Commoncss/imageslider.css']
})
export class DashboardTicketsComponent implements OnInit {
  @ViewChild('scrollMe') private myScrollContainer: ElementRef;
  @ViewChild(ScrollToBottomDirective)
  scroll: ScrollToBottomDirective;
  Link:any
  sub: any;
  docid: string;
  TicketData= [];
  FebData=[]
  MarData=[]
  AprData=[]
  UserSelectedTopic: any;
  selected:boolean = false
  Discription: any;
  OpenedDate: any;
  fullDetailUser;
  TimeLineDetails;
  Selectedmonth: any;
  Timelineid: any;
  Archived : boolean = false
  Now = new Date();
  CloseDate;
  SingleImage: any;
  OwnerChat =[]
  chatarea;
  Messages;
  overlay:boolean = false
  AllImages =[]
  imageSlider:boolean =false
  OverlayMessage;
  matchesValue: any;
  openTickets =[];
  public now: Date = new Date();
  searchEmail;
  search:boolean = false
  constructor(public ActivatedRoute:ActivatedRoute,public AddUserService:AddUserService,private firestore: AngularFirestore) { }

  ngOnInit(): void {
    this.sub = this.ActivatedRoute.paramMap.subscribe((params) => {
      this.docid =  params.get("id")
    })
    // var element = document.getElementById("scrollMe");
    // element.scrollTop = element.scrollHeight;
    //    window.setInterval(function() {
    //     var elem = document.getElementById('scrollMe');
    //     elem.scrollTop = elem.scrollHeight;
    // }, 300);
   // window.scrollTo(300,document.querySelector(".all-chats-area").scrollHeight);


    this.getAllTickets()


  }

 



GetTimeLine()
{

  this.AddUserService.getTickets(this.docid).then((ref)=>{
    ref.forEach(element => {
      this.TicketData.push({Detail:element.data(),id:element.id})
    });

  })

  
}


getSelectedtimeline(id)
{
  this.OverlayMessage ="Getting Details"
  this.overlay = true
  this.AddUserService.getSelectedTickets(this.docid,id).then((ref)=>{
    this.selected = true
    this.fullDetailUser = ref.data()
    this.UserSelectedTopic = id
    this.Discription  = ref.data().Subject
    this.OpenedDate = ref.data().Date.toDate()
    this.Link = ref.data().Link
    this.Timelineid = id
    this.SingleImage = ref.data().Images[0]
    this.AllImages = ref.data().Images
    if(ref.data().Close != null)
    {
      this.CloseDate = ref.data().Close.toDate()
     
    }
 
  if(ref.data().Archived == "true")
  {
    this.Archived = true
  }
  else
  {
    this.Archived = false
  }
  this.overlay = false

  })

}


CloseTicket(UserDetail)
{
  this.TimeLineDetails ={
    Id:this.Timelineid,
    Subject:UserDetail.Subject,
    Date:UserDetail.Date,
    Link:UserDetail.Link,
    Images:UserDetail.Images,
    Archived:"true",
    Close:this.Now
  }
  
  this.AddUserService.updateTickets(this.docid,this.Timelineid,this.TimeLineDetails).then(element => {
    this.Archived = true
   //this.openCloseTicket()
   this.TicketData = []
   this.GetTimeLine()
  })
}


OpenTicket(UserDetail)
{
  
  this.TimeLineDetails ={
    Id:this.Timelineid,
    Subject:UserDetail.Subject,
    Date:UserDetail.Date,
    Link:UserDetail.Link,
    Images:UserDetail.Images,
    Archived:"false",
    Close:null
  }
  this.AddUserService.updateTickets(this.docid,this.Timelineid,this.TimeLineDetails).then(element => {
    this.Archived = false
    //this.openCloseTicket()
    this.TicketData = []
    this.GetTimeLine()
  })
}


openCloseTicket(id,docid)
{
this.overlay = false
this.docid = docid
this.Timelineid = id
  this.AddUserService.getSelectedTickets(this.docid,this.Timelineid).then((ref)=>{

    this.selected = true
    this.fullDetailUser = ref.data()
    this.UserSelectedTopic = this.Timelineid
    this.Discription  = ref.data().Subject
    this.OpenedDate = ref.data().Date.toDate()
    this.Link = ref.data().Link
    this.Timelineid =this.Timelineid
    this.SingleImage = ref.data().Images[0]
    this.AllImages = ref.data().Images
    if(ref.data().Close != null)
    {
      this.CloseDate = ref.data().Close.toDate()
     
    }
 
  if(ref.data().Archived == "true")
  {
    this.Archived = true
  }
  else
  {
    this.Archived = false
  }
 
  
  })
  this.getChat()

}

getChat()
{

  this.OverlayMessage ="Getting Details"
  this.overlay = true
  this.OwnerChat=[]

  this.AddUserService.getTicketsChat(this.docid,this.Timelineid).then(ref => {
    ref.forEach(element => {
      this.OwnerChat.push(element.data())
    });
    this.OwnerChat.sort(function (a, b) {
      return a.Time.seconds  - b.Time.seconds
    });
    this.overlay = false
   })
  
}
CreateUserChat()
{
  this.Now = new Date();
  this.OwnerChat =[]
  var n = this.Now.getSeconds()
  this.Messages ={
    Message:this.chatarea,
    Time:this.Now,
    User:"Owner"
  }
 
       this.AddUserService.CreateTicketChat(this.docid,this.Timelineid,this.Messages).then(element => {
        this.chatarea = null
        this.OverlayMessage ="Getting Chats"
        this.OwnerChat=[]
        this.AddUserService.getTicketsChat(this.docid,this.Timelineid).then(ref => {
          ref.forEach(element => {
            this.OwnerChat.push(element.data())
          });
          this.OwnerChat.sort(function (a, b) {
            return a.Time.seconds  - b.Time.seconds
          });
          this.overlay = false
         })
    })
  
}

openOverLay()
{
  this.imageSlider = true
}
closeImageSlider()

{
  this.imageSlider = false
}

getAllTickets()
{


  this.AddUserService.getAllTickets().snapshotChanges().pipe(
    map(changes =>
      changes.map(c =>
        ({ id: c.payload.doc.id, ...c.payload.doc.data() })
      )
    )
  ).subscribe(data => {
    data.forEach((datas) => {
      this.matchesValue = datas
      this.TicketData.push({Detail:datas,id:datas.id})
     
    })

   
  });
  
}
SearchEmail() {
  this.TicketData =[]
  this.search = true
  this.AddUserService.getAllTickets().snapshotChanges().pipe(
    map(changes =>
      changes.map(c =>
        ({ id: c.payload.doc.id, ...c.payload.doc.data() })
      )
    )
  ).subscribe(data => {
    data.forEach((datas) => {
      this.matchesValue = datas

      if(this.matchesValue.Archived == 'false')
      {
        if ( this.matchesValue .Link.toLowerCase().includes(this.searchEmail.toLowerCase()) ||
        this.matchesValue.Subject.toLowerCase().includes(this.searchEmail.toLowerCase()) ||
       datas.id.toLowerCase().includes(this.searchEmail.toLowerCase())
        ) {

          this.TicketData.push({Detail:datas,id:datas.id})

      }

        this.TicketData.push({Detail:datas,id:datas.id})
      
      }
    })
   
  });
 


  
  






}


CloseSearch() {
  this.searchEmail = null
  this.search = false
  this.TicketData =[]
  this.getAllTickets()


}
}


