import { templateJitUrl } from '@angular/compiler';
import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, ParamMap } from "@angular/router";
import {AddUserService} from "../../Services/add-user.service"
import { Location } from '@angular/common';
import {AuthService} from '../../auth.service'

@Component({
  selector: 'app-user-detail-result',
  templateUrl: './user-detail-result.component.html',
  styleUrls: ['./user-detail-result.component.css','../Admin-common.css','../../Commoncss/popup.css']
})
export class UserDetailResultComponent implements OnInit {
editDetail:boolean = false
fullname:any 
companyname:any 
email:any
nameWebiste:any 
linkwebsite:any 
webDate:any 
  sub: any;
  docid: string;
  Name: any;
  CompanyName: any;
  website: any;
  startdate: any;
  CutomerDetail;
  OverLay:boolean = false
  title;
  Phone;
  postcode;
  country;
  towncity;
  street;
  fieldError: boolean = false
  updatePass:boolean = false
  password;
  hide:boolean = true
  confirmpassword;
  OverLayMessage
  constructor(public ActivatedRoute:ActivatedRoute,public AddUserService:AddUserService,public _location:Location,public AuthService:AuthService) { }

  ngOnInit(): void {
    const UserDetail =  JSON.parse(localStorage.getItem('user'));
    if( UserDetail != null)
    {
    this.docid = UserDetail.uid
    this.getUserDetail()
    }
  }




  getUserDetail()
  {
    this.AddUserService.getselectedUsers(this.docid).then((element)=>{
      this.fullname = element.data().fullname
      this.CompanyName = element.data().CompanyName
      this.email = element.data().email
      //this.website = element.data().website
      this.linkwebsite = element.data().linkwebsite

      this.startdate = element.data().startdate.toDate()

        if(element.data().title != undefined)
        {
          this.title = element.data().title
        }
        if(element.data().Phone != undefined)
        {
          this.Phone = element.data().Phone
        }
        if(element.data().postcode != undefined)
        {
          this.postcode = element.data().postcode
        }
        if(element.data().country != undefined)
        {
          this.country = element.data().country
        }
        if(element.data().towncity != undefined)
        {
          this.towncity = element.data().towncity
        }
        if(element.data().street != undefined)
        {
          this.street = element.data().street
        }
    })
  }

 Update()
  {
    this.OverLayMessage = "User Updated Successfully"
    this.CutomerDetail = {
      fullname:this.fullname,
      CompanyName: this.CompanyName,
      email: this.email,
      linkwebsite: this.linkwebsite,
      startdate: this.startdate,
      title:this.title,
      Phone:this.Phone,
      postcode:this.postcode,
      country:this.country,
      towncity:this.towncity,
      street:this.street,
      Archieve: "false",
      user:'user'
    }
    this.AddUserService.updateLeads(this.docid,this.CutomerDetail).then(element => {
     this.OverLay = true
     this.getUserDetail()
    })
  }
  editDetailUser()
  {
   this.editDetail = true
  }
  backeditDetailUser()
  {
   this.editDetail = false
  }
  backed() {
    //this._location.back()
    this.OverLay = false
    this.editDetail = false
    this.getUserDetail()
  }

  checkUserValidation()
  {
    if(this.title == null || this.fullname == null || this.email == null || this.CompanyName == null || this.Phone == null || this.postcode == null || this.country == null || this.towncity == null || this.street == null )
    {
      this.fieldError = true
    }
    else
    {
      this.Update()
    }
  }

  passwordUpdate()

  {
    if(this.password != this.confirmpassword)
    {
      this.OverLayMessage = "Both Password Does not match"
      this.OverLay = true
    }
    else
    {
this.AuthService.UpdatePassword(this.confirmpassword)
this.updatePass = false
this.OverLayMessage = "User password updated suceesfully"
this.OverLay = true
    }
  }

  
  //
}
