import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import {AdminPanelComponent} from './admin-panel/admin-panel.component'
import { UserPageComponent}  from "./admin-panel/user-page/user-page.component"
import {AddUserComponent} from "./admin-panel/add-user/add-user.component"
import {UserDetailComponent} from "./admin-panel/user-detail/user-detail.component"
import {AddTicketComponent} from"./admin-panel/add-ticket/add-ticket.component"
import {AddTimeLineComponent} from "./admin-panel/add-time-line/add-time-line.component"
import {ArchieveComponent} from "./admin-panel/archieve/archieve.component"
import {TimeLineComponent} from "./admin-panel/time-line/time-line.component"
import {TicketsComponent} from './admin-panel/tickets/tickets.component'
import {PaymentsComponent} from './admin-panel/payments/payments.component'
import {MobileOpenTimelineComponent}  from './admin-panel/mobile-open-timeline/mobile-open-timeline.component'
import { MobileOpenTicketsComponent} from "./admin-panel/mobile-open-tickets/mobile-open-tickets.component"
const routes: Routes = [
  { path: "", component: AdminPanelComponent },
  { path: "UserPanel", component: UserPageComponent },
  {path:'AddUser',component:AddUserComponent},
  {path:'UserDetail/:id/:type',component:UserDetailComponent},
  {path:'AddTicket/:id', component:AddTicketComponent},
  {path:'AddTimeLine/:docid',component:AddTimeLineComponent},
  {path:'Archieve',component:ArchieveComponent},
  {path:'TimeLine/:type/:month' , component:TimeLineComponent},
  {path:'Tickets/:Id' , component:TicketsComponent},
  {path:'Payment' , component:PaymentsComponent},
  {path:'MobileOpenTimeline/:id/:month' ,component :MobileOpenTimelineComponent},
  {path:'MobileOpenTickets/:id' ,component :MobileOpenTicketsComponent}

  
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
