import { Component, OnInit } from '@angular/core';
import {AuthService} from '../../auth.service';
import {AddUserService} from "../../Services/add-user.service"
@Component({
  selector: 'app-admin-header',
  templateUrl: './admin-header.component.html',
  styleUrls: ['./admin-header.component.css']
})
export class AdminHeaderComponent implements OnInit {
UserPanel:boolean = false
dashboard:boolean = false
Archieve :boolean = false
  origin: string;
  pay:boolean = false
  Notify:boolean  = false
  TicketData =[]
  JanuaryData =[]
  pays = []
  docid 
  interval: number;
  constructor(public AuthService:AuthService,public AddUserService:AddUserService) { }

  ngOnInit(): void {
    this.origin = window.location.origin
    var pathname = new URL(window.location.href).pathname;
    const UserDetail =  JSON.parse(localStorage.getItem('user'));
    if(UserDetail != null){
      this.docid = UserDetail.uid
    }


    if(window.location.href ==  this.origin +"/")
    {
      this.dashboard = true
      this.UserPanel = false
      this.Archieve = false
      this.pay = false
    }
    if(pathname.includes("TimeLine") == true)
    {
      this.dashboard = false
      this.UserPanel = true
      this.Archieve = false
      this.pay = false

    }

    if(pathname.includes("Tickets") == true ||  pathname.includes("AddTicket") == true)
    {
      this.dashboard = false
      this.UserPanel =false
      this.Archieve = true
      this.pay = false

    }
    if(pathname.includes("Payment") == true)
    {
      this.dashboard = false
      this.UserPanel =false
      this.Archieve = false
      this.pay = true

    }

   
    // if(window.location.href !=  this.origin +"/" && window.location.href != this.origin + "/TimeLine" && window.location.href != this.origin + "/Tickets" && window.location.href != this.origin + "/Payment")
    // {
    //   this.dashboard = false
    //   this.UserPanel = true
    //   this.Archieve = false
    //   this.pay = false
    // }
    this.GetTiicket()
    this.GetTimeLine() 
    this.GetPayment()
  //      this.interval = setInterval(() => { 
  //       this.GetTiicket()
  //       this.GetTimeLine() 
  //       this.GetPayment()
  // }, 1000);
  }

  GetTiicket()
  {
    this.TicketData =[]
    const Tickets = "Tickets"
    this.AddUserService.getNoti(this.docid,Tickets).then((ref)=>{
      ref.forEach(element => {
        if(element.data().Close == null)
        {
          this.TicketData.push({Detail:element.data(),id:element.id})

        }
      });
      this.TicketData.sort(function (a, b) {
        return a.Detail.Date.seconds - b.Detail.Date.seconds
      });
     
    })
  
    
  }
  
  GetTimeLine() {
    const Timeline= "Timeline"
        this.AddUserService. getNoti(this.docid,Timeline).then((ref) => {
          ref.forEach(element => {
            if(element.data().Close == null)
            {
            this.JanuaryData.push({ Detail: element.data(), id: element.id, month: "Jan" })
            }
          });
          this.JanuaryData.sort(function (a, b) {
            return a.Detail.Date.seconds - b.Detail.Date.seconds
          });
        })
      }
  
      GetPayment() {
        const Timeline= "Payment"
            this.AddUserService. getNoti(this.docid,Timeline).then((ref) => {
              ref.forEach(element => {
                if(element.data().close == null)
                {
                this.pays.push({ Detail: element.data(), id: element.id})
                }
              });
              this.pays.sort(function (a, b) {
                return a.Detail.date.seconds - b.Detail.date.seconds
              });
            })
          }
  


  LogOut()
  {
    this.AuthService.SignOut()
  }
}
