import { Component, OnInit } from '@angular/core';
import {AddUserService} from "../../Services/add-user.service"
import { Router, ActivatedRoute, ParamMap } from "@angular/router";
import { elementAt } from 'rxjs/operators';
@Component({
  selector: 'app-payments',
  templateUrl: './payments.component.html',
  styleUrls: ['./payments.component.css','../Admin-common.css','../../Commoncss/popup.css','../../Commoncss/imageslider.css']
})
export class PaymentsComponent implements OnInit {
  Amount;
  ModeofPayment;
  date;
  Payments
  sub: any;
  docid: string;
  OverlayMessage;
  loader:boolean = false
  OverLay:boolean = false
  Allpayments =[]
  constructor(public AddUserService:AddUserService,public ActivatedRoute:ActivatedRoute) { }

  ngOnInit(): void {
    const UserDetail =  JSON.parse(localStorage.getItem('user'));
    this.docid = UserDetail.uid

    this.getAllPayments()
  }





  AddPayment()
  {
    this.OverlayMessage = "Adding payment"
    this.loader = true
    this.Payments = {
      Amount:this.Amount,
      ModeofPayment:this.ModeofPayment,
      date:this.date,
      openDate:null
    }
    this.AddUserService.CreatePaymennt(this.docid,this.Payments).then((element)=>{
      this.loader = false
      this.OverLay = true
      this.getAllPayments()
    })
  }

  ClosePopup()
  {
    this.OverLay = false
  }

  getAllPayments()
  {
    this.Allpayments = []
    this.OverlayMessage = "Getting  payments"
 this.loader = true
    this.AddUserService.getPayment(this.docid).then((ref)=>{
      ref.forEach(element => {
        this.Allpayments.push(element.data())

      });
      this.Allpayments.sort(function (a, b) {
        return a.date.seconds - b.date.seconds
      });
      this.loader = false
    })
  }
}
