import { Component, OnInit } from '@angular/core';
import { AddUserService } from "../../Services/add-user.service";
import { Location } from '@angular/common';
import {AuthService} from "../../auth.service"
import { Injectable, NgZone } from '@angular/core';
import { AngularFireAuth } from "@angular/fire/auth";
import { AngularFirestore, AngularFirestoreDocument } from '@angular/fire/firestore';
import { Router } from "@angular/router";
@Component({
  selector: 'app-add-user',
  templateUrl: './add-user.component.html',
  styleUrls: ['./add-user.component.css', '../Admin-common.css']
})
export class AddUserComponent implements OnInit {
  CutomerDetail;
  fullname;
  CompanyName;
  email;
  website;
  linkwebsite;
  startdate;
  OverLay: boolean = false
  password: string;
  hide:boolean = true

  constructor(public AddUserService: AddUserService, private _location: Location,public AuthService:AuthService,    public afs: AngularFirestore,   // Inject Firestore service
    public afAuth: AngularFireAuth, // Inject Firebase auth service
    public router: Router,  
    public ngZone: NgZone ) { }

  ngOnInit(): void {
  }








  CreateCustomer() {
    this.CutomerDetail = {
      fullname: this.fullname,
      CompanyName: this.CompanyName,
      email: this.email,
      website: this.website,
      linkwebsite: this.linkwebsite,
      startdate: this.startdate,
      Archieve: "false"
    }
    this.AddUserService.CreateCustomer(this.CutomerDetail).then(element => {
      if (element == true) {
        this.OverLay = true
      }

    })
  }
  backed() {
    this.router.navigate(['/UserPanel']).then(() => {
      window.location.reload();
    });
    this.OverLay = false
  }


  SignUp() {
    const RemEmail = JSON.parse(localStorage.getItem('Loginemail'));
    const Rempassword = JSON.parse(localStorage.getItem('Loginpassword'));
    return this.afAuth.createUserWithEmailAndPassword(this.email, this.password)
      .then((result) => {
        console.log(result)
        if(result.user != null){
       const  user  = result.user
        const userRef: AngularFirestoreDocument<any> = this.afs.doc(`users/${user.uid}`);
        const userData: any = {
          uid: user.uid,
          email: user.email,
          displayName: user.displayName,
          photoURL: user.photoURL,
          emailVerified: user.emailVerified,
          fullname: this.fullname,
          CompanyName: this.CompanyName,
          emails: this.email,
          website: this.website,
          linkwebsite: this.linkwebsite,
          startdate: this.startdate,
          Archieve: "false",
          user:"user"
        }
        return userRef.set(userData, {merge: true}),  
      this.afAuth.signInWithEmailAndPassword(RemEmail, Rempassword)
        .then((result) => {this.ngZone.run(() => {
           // this.router.navigate(['dashboard']);
           this.OverLay = true
          });
          
        })
      }
       // this.SignIn("sourcecode@test.com", "admin@123")
      }).catch((error) => {
        window.alert(error.message)
      })
  }
  

  CreateUser(email,password)
  {
    this.AuthService.SignUp(email,password)
  }
}
