import { Component, OnInit } from '@angular/core';
import { from } from 'rxjs';
import {AddUserService} from "../../Services/add-user.service"
import { Location } from '@angular/common';
import firebase from 'firebase/app';
@Component({
  selector: 'app-archieve',
  templateUrl: './archieve.component.html',
  styleUrls: ['./archieve.component.css','../Admin-common.css','../../Commoncss/popup.css']
})
export class ArchieveComponent implements OnInit {
  UserDetaildata = []
 CutomerDetail;
 searchEmail;
 OverLay:boolean = false;
 Search:boolean = false
 message;
 getDetail;
  constructor(public AddUserService:AddUserService,public _location:Location) { }

  ngOnInit(): void {
    this.getUserDetail()
  }



  getUserDetail()
  {
    this.AddUserService. getUser().then(ref=>{
      ref.forEach(element => {
        this.UserDetaildata.push({Detail:element.data(),Id:element.id})
      });
   })
  }


  removeSearch()
  {
    this.UserDetaildata = []
    this.Search = false
    this.getUserDetail()
  }

  SearchEmail()
  {
    this.UserDetaildata = []
    this.Search = true
    this.AddUserService.getUser().then(ref=>{
      ref.forEach(element => {
       this.getDetail = element.data()
        if( this.getDetail.email.toLowerCase().includes(this.searchEmail.toLowerCase())||
        this.getDetail.CompanyName.toLowerCase().includes(this.searchEmail.toLowerCase()) ||
        this.getDetail.fullname.toLowerCase().includes(this.searchEmail.toLowerCase()) ||
        this.getDetail.website.toLowerCase().includes(this.searchEmail.toLowerCase()) ||
        this.getDetail.linkwebsite.toLowerCase().includes(this.searchEmail.toLowerCase()) ||
        this.getDetail.linkwebsite.toLowerCase().includes(this.searchEmail.toLowerCase())
        )
        {
      
          this.UserDetaildata.push({Detail:element.data(),Id:element.id})

        }

      });
   })
  }
  Archieve(Detail,id)

  {
    this.message = "User Retored Successfully"
    this.CutomerDetail = {
      fullname: Detail.fullname,
      CompanyName: Detail.CompanyName,
      email: Detail.email,
      website: Detail.website,
      linkwebsite: Detail.linkwebsite,
      startdate: Detail.startdate,
      Archieve: "false",
      user:"user"
    }
    this.AddUserService.updateLeads(id,this.CutomerDetail).then(element => {
      this.OverLay = true
  
    })
  }
  backed() {
    this.OverLay = false
    this.UserDetaildata =[]
    this.getUserDetail()
  }

  DeleteProperty(id)
  {
    this.message = "User Deleted Successfully"
    this.AddUserService.delete(id).then(element => {
      this.OverLay = true
       var user = firebase.auth().currentUser;
      console.log(user)
      // user.delete();
    })
  }

}

