  getSelectedtimeline(month, id) {
    this.OverlayMessage = "Getting Details"
    this.overlay = true
    this.AddUserService.getSelectedTimeline(this.docid,month,id).then((ref) => {
      this.selected = true
      this.fullDetailUser = ref.data()
      this.UserSelectedTopic = ref.data().Topic
      this.Discription = ref.data().Discription
      this.OpenedDate = ref.data().Date.toDate()
      this.Link = ref.data().Link
      this.Selectedmonth = month
      this.Timelineid = id
      if(ref.data().Images != null)
      {
        this.SingleImage = ref.data().Images[0]
       
      }
      this.AllImages = ref.data().Images
      if (ref.data().Close != null) {
        this.CloseDate = ref.data().Close.toDate()

      }

      if (ref.data().Archived == "true") {
        this.Archived = true
      }
      else {
        this.Archived = false
      }
      this.overlay = false
      this.getChat()
    })
  }

  CloseTicket(UserDetail) {
    this.TimeLineDetails = {
      Topic: UserDetail.Topic,
      Discription: UserDetail.Discription,
      Date: UserDetail.Date,
      Link: UserDetail.Link,
      Images: UserDetail.Images,
      Archived: "true",
      Close: this.Now
    }
    this.AddUserService.updateTimeline(this.docid, this.Selectedmonth, this.Timelineid, this.TimeLineDetails).then(element => {
      this.Archived = true
      this.openCloseTicket()
    })
  }


  OpenTicket(UserDetail) {

    this.TimeLineDetails = {
      Topic: UserDetail.Topic,
      Discription: UserDetail.Discription,
      Date: UserDetail.Date,
      Link: UserDetail.Link,
      Images: UserDetail.Images,
      Archived: "false",
      Close: null
    }
    this.AddUserService.updateTimeline(this.docid, this.Selectedmonth, this.Timelineid, this.TimeLineDetails).then(element => {
      this.Archived = false
      this.openCloseTicket()
    })
  }


  openCloseTicket() {
    this.AddUserService.getSelectedTimeline(this.docid, this.Selectedmonth, this.Timelineid).then((ref) => {
      this.selected = true
      this.fullDetailUser = ref.data()
      this.UserSelectedTopic = ref.data().Topic
      this.Discription = ref.data().Discription
      this.OpenedDate = ref.data().Date.toDate()
      this.Link = ref.data().Link
      this.Selectedmonth = this.Selectedmonth
      this.Timelineid = this.Timelineid
      this.SingleImage = ref.data().Images[0]
      this.AllImages = ref.data().Images
      if (ref.data().Close != null) {
        this.CloseDate = ref.data().Close.toDate()

      }

      if (ref.data().Archived == "true") {
        this.Archived = true
      }
      else {
        this.Archived = false
      }
      this.overlay = false

    })
  }
  getChat() {

    this.OverlayMessage = "Getting Details"
    this.overlay = true
    this.OwnerChat = []
    this.AddUserService.getChat(this.docid, this.Timelineid).then(ref => {
      ref.forEach(element => {
        this.OwnerChat.push(element.data())
      });
      this.OwnerChat.sort(function (a, b) {
        return a.Time.seconds - b.Time.seconds
      });
      this.overlay = false
    })

  }
  CreateUserChat() {


    this.Now = new Date();
    this.OwnerChat = []
    var n = this.Now.getSeconds()
    this.Messages = {
      Message: this.chatarea,
      Time: this.Now,
      User: "User"
    }

    this.AddUserService.CreateTChat(this.docid, this.Timelineid, this.Messages).then(element => {
      this.createChatNotification(this.Timelineid,this.Selectedmonth)
      this.chatarea = null
      this.OverlayMessage = "Getting Chats"
      this.OwnerChat = []
      this.AddUserService.getChat(this.docid, this.Timelineid).then(ref => {
        ref.forEach(element => {
          this.OwnerChat.push(element.data())
        });
        this.OwnerChat.sort(function (a, b) {
          return a.Time.seconds - b.Time.seconds
        });

      })
    })

  }

