getAllTickets()
{

  this.AddUserService.getAllTickets().snapshotChanges().pipe(
    map(changes =>
      changes.map(c =>
        ({ id: c.payload.doc.id, ...c.payload.doc.data() })
      )
    )
  ).subscribe(data => {
    data.forEach((datas) => {
      this.matchesValue = datas      
      if(this.matchesValue.Archived != 'true' )
      {
        this.openTickets.push(this.matchesValue)
      }
    })
  

  });
}
OpenTickets()
