import { Component, OnInit } from '@angular/core';
import { Location } from '@angular/common';
import {AddUserService} from "../../Services/add-user.service"
import { Router, ActivatedRoute, ParamMap } from "@angular/router";
import {
  AngularFireStorage,
  AngularFireUploadTask,
} from '@angular/fire/storage';
@Component({
  selector: 'app-add-ticket',
  templateUrl: './add-ticket.component.html',
  styleUrls: ['./add-ticket.component.css', '../Admin-common.css','../../Commoncss/popup.css']
})
export class AddTicketComponent implements OnInit {
  sub: any;
  docid: string;
  topic;
  Discription;
  Message;
  Date = new Date();
  Link;
  TimeLineDetails;
  Messages;
  UpdateMessage;
  isLoading:boolean = false
  OverLay:boolean = false
  public now: Date = new Date();
  uploads;
  UploadedImages =[];
  LoaderMessage;
  Now = new Date();
  fieldError :boolean = false
  constructor(public _location:Location,public AddUserService:AddUserService,
    public ActivatedRoute:ActivatedRoute,
    private afStorage: AngularFireStorage,
    public Router:Router) { }

  ngOnInit(): void {

    const UserDetail =  JSON.parse(localStorage.getItem('user'));
    this.docid = UserDetail.uid

 
  }




  createTimeLine()
  {
    this.LoaderMessage = "Creating Ticket...."
    this.isLoading = true
     this.UpdateMessage = "Ticket Created Successfully"
    var dateObj = new Date(this.Date);
    var month = dateObj.getMonth() + 1; //months from 1-12
    var day = dateObj.getUTCDate();
    var year = dateObj.getUTCFullYear() ;
    const newdate = year + "/" + month + "/" + day ;
    const SingleMonth =  dateObj.toLocaleString('default', { month: 'short' })
    if(this.Link == undefined)
    {
      this.Link = null
    }
    if(this.UploadedImages.length == 0)
    {
      this.UploadedImages = null
    }
    this.TimeLineDetails ={
      Subject:this.Discription,
      Date:this.Date,
      Link:this.Link,
      Images:this.UploadedImages,
      Archived:"false",
      Close:null,
      docid:this.docid,
      openDate:null
    }
    this.Messages ={
      Message:this.Message,
      Time:this.now,
      User:"User"
    }
 
    this.AddUserService.CreateTicket(this.docid,this.TimeLineDetails).then(element => {
      const UserId =  JSON.parse(localStorage.getItem('CreateTicketId'));
      this.UpdateTicket(UserId )
    
      this.AddUserService.CreateTicketChat(this.docid, UserId ,this.Messages).then(element => {
        this.isLoading = false
        this.OverLay = true
      })
    })

  }
  importImages(event) {
    this.LoaderMessage = "Uploading Images..."
    this.isLoading = true
    this.uploads = [];

    const filelist = event.target.files;
    if (filelist.length > 8) {
      this.UpdateMessage = "Upload Maximum  8 Images" 
      this.OverLay = true
          this.isLoading = false
    }
    else {
      for (const file of filelist) {
        if (file.size > 2000000) {   
          this.UpdateMessage = "Images Size must be 2MB"
          this.OverLay = true
          this.isLoading = false
        }
        else {

          const path = `sellerfiles/${file.name}`;
          const ref = this.afStorage.ref(path);
          const task = this.afStorage.upload(path, file);
          const _percentage$ = task.percentageChanges();
          // create composed objects with different information. ADAPT THIS ACCORDING to YOUR NEED
          const uploadTrack = {
            fileName: file.name,
          }
          // push each upload into the array
          this.uploads.push(uploadTrack);
          if (this.uploads > 8) {
            this.UpdateMessage = "Upload Max 8 Images"
            this.OverLay = true
            this.isLoading = false
          }
          else {
            // for every upload do whatever you want in firestore with the uploaded file
            const _t = task.then((f) => {
              return f.ref.getDownloadURL().then((url) => {
                this.UploadedImages.push(url)
                // return this.afs.collection('files').add({
                //   name: f.metadata.name,

                // });
                this.isLoading = false
              })
            })
          }

        }
      }
    }
  
  }



  backed() {
 // this._location.back()
 this.OverLay = false
 this.Router.navigate(['/Tickets/Id'])
  }

  CreateUserChat()
  {
    this.Now = new Date();
    this.Messages ={
      Message:this.Message,
      Time:this.Now,
      User:"User"
    }

         this.AddUserService. CreateTTickets(this.docid,this.Messages).then(element => {
        this.isLoading = false
        this.OverLay = true
      })
    
  }
  UpdateTicket(UserDetail)
{
  this.TimeLineDetails ={
    Id:UserDetail,
    Subject:this.Discription,
      Date:this.Date,
      Link:this.Link,
      Images:this.UploadedImages,
      Archived:"false",
      Close:null,
      docid:this.docid,
      OwnerMessage:"true",
      UserMessage:"false"
  }
  
  this.AddUserService.updateTickets(this.docid,UserDetail,this.TimeLineDetails).then(element => {

  })
}
checkFields()
{
 if(this.Discription == undefined || this.Message == undefined  || this.Date == null  )
 {
  this.fieldError = true
 }
 else
 {
   this.createTimeLine()
 }
}


}