  archivevalue
  constructor(public ActivatedRoute:ActivatedRoute,public AddUserService:AddUserService,private firestore: AngularFirestore,private _location: Location) { }

  ngOnInit(): void {
    const UserDetail =  JSON.parse(localStorage.getItem('user'));
    this.docid = UserDetail.uid

    this.sub = this.ActivatedRoute.paramMap.subscribe((params)=>{
      this.IdUser = params.get('id')
    })
    

  

    if(this.IdUser != null)
    {
      this.getSelectedtimeline(this.IdUser)
    }

    
  

  }

 



GetTimeLine()
{
  this.TicketData =[]
  this.loader = true
  this.AddUserService.getTickets(this.docid).then((ref)=>{
    ref.forEach(element => {
  
      this.TicketData.push({Detail:element.data(),id:element.id})
    });
    this.loader = false
  })

  
}


getSelectedtimeline(id)
{
  this.OverlayMessage ="Getting Details"
  this.overlay = true
this. showId = true

console.log(id)
  this.AddUserService.getSelectedTickets(this.docid,id).then((ref)=>{
    this.selected = true
    //document.getElementById("Active-ticket").style.borderRight = "3px solid #113c73";
    
    this.fullDetailUser = ref.data()
    this.UserSelectedTopic = id
    this.Discription  = ref.data().Subject
    this.OpenedDate = ref.data().Date.toDate()
    this.Link = ref.data().Link
    this.Timelineid = id
    if(ref.data().Images != null)
    {
      this.SingleImage = ref.data().Images[0]
      this.AllImages = ref.data().Images
    }
   

    
   

    this.arch = ref.data().Archived
    if(ref.data().Close != null)
    {
      this.CloseDate = ref.data().Close.toDate()
     
    }
    else
    {
      this.CloseDate = null
    }
 
  if(ref.data().Archived == "true")
  {
    this.Archived = true
    this. archivevalue = 'true'
  }
  else
  {
    this.Archived = false
    this.archivevalue = 'false'
    
  }
  this.overlay = false

this.getChat()
this.updateTicket(id)
  })

}


CloseTicket(UserDetail)
{
  this.TimeLineDetails ={
    Id:this.Timelineid,
    Subject:UserDetail.Subject,
    Date:UserDetail.Date,
    Link:UserDetail.Link,
    Images:UserDetail.Images,
    Archived:"true",
    Close:this.Now,
    docid:this.docid,
    OwnerMessage:"false",
    UserMessage:"false"
  }
  
  this.AddUserService.updateTickets(this.docid,this.Timelineid,this.TimeLineDetails).then(element => {
    this.Archived = true
   this.openCloseTicket()
   this.TicketData = []
   this.GetTimeLine()
  })
}


OpenTicket(UserDetail)
{
  
  this.TimeLineDetails ={
    Id:this.Timelineid,
    Subject:UserDetail.Subject,
    Date:UserDetail.Date,
    Link:UserDetail.Link,
    Images:UserDetail.Images,
    Archived:"false",
    Close:null,
    docid:this.docid,
    OwnerMessage:"false",
    UserMessage:"false"
  }
  this.AddUserService.updateTickets(this.docid,this.Timelineid,this.TimeLineDetails).then(element => {
    this.Archived = false
    this.openCloseTicket()
    this.TicketData = []
    this.GetTimeLine()
  })
}


openCloseTicket()
{
this.overlay = false

  this.AddUserService.getSelectedTickets(this.docid,this.Timelineid).then((ref)=>{
    this.selected = true
    this.fullDetailUser = ref.data()
    this.UserSelectedTopic = this.Timelineid
    this.Discription  = ref.data().Subject
    this.OpenedDate = ref.data().Date.toDate()
    this.Link = ref.data().Link
    this.Timelineid =this.Timelineid

    if(ref.data().Images != null)
    {
      this.SingleImage = ref.data().Images[0]
    }

    this.AllImages = ref.data().Images
    this.arch = ref.data().Archived
    if(ref.data().Close != null)
    {
      this.CloseDate = ref.data().Close.toDate()
     
    }
 
  if(ref.data().Archived == "true")
  {
    this.Archived = true
  }
  else
  {
    this.Archived = false
  }
 
  
  })

}

getChat()
{

  this.OverlayMessage ="Getting Details"
  this.overlay = false


  this.AddUserService.getTicketsChat(this.docid,this.Timelineid).then(ref => {
    ref.forEach(element => {
      this.OwnerChat.push(element.data())
    });
    this.OwnerChat.sort(function (a, b) {
      return a.Time.seconds  - b.Time.seconds
    });
    this.overlay = false
   })
  
}
CreateUserChat()
{
  this.Now = new Date();
  this.OwnerChat =[]
  var n = this.Now.getSeconds()
  this.Messages ={
    Message:this.chatarea,
    Time:this.Now,
    User:"User"
  }
 
       this.AddUserService.CreateTicketChat(this.docid,this.Timelineid,this.Messages).then(element => {
        this.chatarea = null
        this.OverlayMessage ="Getting Chats"
        this.OwnerChat=[]
        this.AddUserService.getTicketsChat(this.docid,this.Timelineid).then(ref => {
          ref.forEach(element => {
            this.OwnerChat.push(element.data())
          });
          this.OwnerChat.sort(function (a, b) {
            return a.Time.seconds  - b.Time.seconds
          });
          this.overlay = false
         })
    })
  this.UpdateCreateChatTicket(this.Timelineid)
}

openOverLay()
{
  this.imageSlider = true
}
closeImageSlider()

{
  this.imageSlider = false
}

save(event){
  this.SearchEmail()
}
SearchEmail() {
  this.TicketData =[]
  this.search = true

  this.AddUserService.getTickets(this.docid).then((ref)=>{
    ref.forEach(element => {
      this.getDetail = element.data()
      if (this.getDetail.Link.toLowerCase().includes(this.searchEmail.toLowerCase()) ||
        this.getDetail.Subject.toLowerCase().includes(this.searchEmail.toLowerCase()) ||
        element.id.toLowerCase().includes(this.searchEmail.toLowerCase())
        
        ) {

          this.TicketData.push({Detail:element.data(),id:element.id})

      }
     
    });

  })
  
  






}
CloseSearch() {
  this.searchEmail = null
  this.search = false

  this.GetTimeLine()


}

updateTicket(UserDetail)
{

  this.TimeLineDetails ={
    Id:UserDetail,
    Subject:this.Discription,
      Date:this.OpenedDate,
      Link:this.Link,
      Images:this.AllImages,
      Archived:this.archivevalue,
      Close:this.CloseDate,
      docid:this.docid,
      OwnerMessage:"false",
      UserMessage:"false"

  }
  
  this.AddUserService.updateTickets(this.docid,UserDetail,this.TimeLineDetails).then(element => {

    
  })
}

UpdateCreateChatTicket(UserDetail)
{
  this.TimeLineDetails ={
    Id:UserDetail,
    Subject:this.Discription,
      Date:this.OpenedDate,
      Link:this.Link,
      Images:this.AllImages,
      Archived:this.arch ,
      Close:this.CloseDate,
      docid:this.docid,
      OwnerMessage:"true",
      UserMessage:"false"

  }
  
  this.AddUserService.updateTickets(this.docid,UserDetail,this.TimeLineDetails).then(element => {
  
  })
}

